import React, { useState, useEffect, useCallback } from 'react';
import Card from '../common/Card';
import Button from '../common/Button';
import MatrixGrid from './MatrixGrid';
import JudgmentHelper from './JudgmentHelper';
import apiService from '../../services/api';

interface CriterionItem {
  id: string;
  name: string;
}

interface ComparisonItem {
  id: string;
  criteriaAId: string;
  criteriaBId: string;
  value: number;
}

interface Matrix {
  id: string;
  name: string;
  items: string[];
  values: number[][];
  completed: boolean;
  consistencyRatio?: number;
}

interface PairwiseEvaluationProps {
  projectId: string;
  projectTitle: string;
  onComplete: () => void;
  onBack: () => void;
}

const PairwiseEvaluation: React.FC<PairwiseEvaluationProps> = ({
  projectId,
  projectTitle,
  onComplete,
  onBack
}) => {
  const [criteria, setCriteria] = useState<CriterionItem[]>([]);
  const [comparisons, setComparisons] = useState<ComparisonItem[]>([]);
  const [evaluationId, setEvaluationId] = useState<string>('');
  const [matrix, setMatrix] = useState<Matrix | null>(null);
  const [showHelper, setShowHelper] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [loadError, setLoadError] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({ type, text });
    setTimeout(() => setActionMessage(null), 4000);
  };

  // criteria + comparisons â†’ ëŒ€ì¹­ í–‰ë ¬ ìƒì„±
  const buildMatrix = useCallback((
    criteriaList: CriterionItem[],
    compList: ComparisonItem[]
  ): Matrix => {
    const n = criteriaList.length;
    const values: number[][] = Array.from({ length: n }, (_, i) =>
      Array.from({ length: n }, (_, j) => {
        if (i === j) return 1;
        const cA = criteriaList[i].id;
        const cB = criteriaList[j].id;
        const comp = compList.find(c =>
          (c.criteriaAId === cA && c.criteriaBId === cB) ||
          (c.criteriaAId === cB && c.criteriaBId === cA)
        );
        if (!comp || comp.value == null) return 1;
        return comp.criteriaAId === cA ? comp.value : 1 / comp.value;
      })
    );
    return {
      id: 'criteria',
      name: 'ê¸°ì¤€ ìŒëŒ€ë¹„êµ',
      items: criteriaList.map(c => c.name),
      values,
      completed: false,
    };
  }, []);

  const loadData = useCallback(async () => {
    if (!projectId) return;
    setIsLoading(true);
    setLoadError(false);
    try {
      // 1. í˜„ì¬ ì‚¬ìš©ìì˜ í‰ê°€ ì¡°íšŒ
      const evalsRes = await apiService.get<unknown>(
        `/api/service/evaluations/evaluations/?project=${projectId}&page_size=10`
      );
      const evalsData = evalsRes.data as { results?: unknown[]; id?: unknown } | unknown[];
      const evalsList: unknown[] = (Array.isArray(evalsData) ? evalsData : (evalsData as { results?: unknown[] }).results) ?? [];
      const evalItem = evalsList[0];
      if (!evalItem) {
        setLoadError(true);
        return;
      }
      const evalId = String((evalItem as { id: unknown }).id);
      setEvaluationId(evalId);

      // 2. ê¸°ì¤€ + ë¹„êµìŒ ë³‘ë ¬ ë¡œë“œ
      const [criteriaRes, comparisonsRes] = await Promise.allSettled([
        apiService.get<unknown>(`/api/service/projects/criteria/?project=${projectId}&page_size=100`),
        apiService.get<unknown>(`/api/service/evaluations/comparisons/?evaluation=${evalId}&page_size=100`),
      ]);

      const criteriaList: CriterionItem[] =
        criteriaRes.status === 'fulfilled'
          ? (() => {
              const d = criteriaRes.value.data as { results?: unknown[] } | unknown[];
              const arr: unknown[] = (Array.isArray(d) ? d : (d as { results?: unknown[] }).results) ?? [];
              return arr.map((c) => {
                const item = c as { id: unknown; name?: string; title?: string };
                return { id: String(item.id), name: item.name || item.title || `ê¸°ì¤€ ${item.id}` };
              });
            })()
          : [];

      const compList: ComparisonItem[] =
        comparisonsRes.status === 'fulfilled'
          ? (() => {
              const d = comparisonsRes.value.data as { results?: unknown[] } | unknown[];
              const arr: unknown[] = (Array.isArray(d) ? d : (d as { results?: unknown[] }).results) ?? [];
              return arr.map((c) => {
                const item = c as { id: unknown; criteria_a: unknown; criteria_b: unknown; value?: number };
                return {
                  id: String(item.id),
                  criteriaAId: String(item.criteria_a),
                  criteriaBId: String(item.criteria_b),
                  value: item.value ?? 1,
                };
              });
            })()
          : [];

      setCriteria(criteriaList);
      setComparisons(compList);

      if (criteriaList.length >= 2) {
        setMatrix(buildMatrix(criteriaList, compList));
      } else {
        setLoadError(true);
      }
    } catch (error) {
      setLoadError(true);
    } finally {
      setIsLoading(false);
    }
  }, [projectId, buildMatrix]);

  useEffect(() => {
    loadData();
  }, [loadData]);

  // í–‰ë ¬ ê°’ ë³€ê²½ â†’ ë¡œì»¬ ìƒíƒœë§Œ ì—…ë°ì´íŠ¸ (ì €ì¥ì€ ì™„ë£Œ ì‹œ)
  const handleMatrixUpdate = useCallback((newValues: number[][]) => {
    setMatrix(prev => prev ? { ...prev, values: newValues } : prev);
  }, []);

  // ì…ë ¥ëœ ì…€ ìˆ˜ ê¸°ë°˜ ì§„í–‰ë¥ 
  const getProgressPercentage = useCallback(() => {
    if (!matrix) return 0;
    const n = matrix.items.length;
    const total = (n * (n - 1)) / 2;
    if (total === 0) return 100;
    let filled = 0;
    for (let i = 0; i < n; i++) {
      for (let j = i + 1; j < n; j++) {
        if (matrix.values[i][j] !== 1) filled++;
      }
    }
    return Math.round((filled / total) * 100);
  }, [matrix]);

  // ì™„ë£Œ: ë¹„êµê°’ ì €ì¥ â†’ CR ê³„ì‚° â†’ onComplete í˜¸ì¶œ
  const handleComplete = async () => {
    if (!evaluationId || !matrix) return;
    setIsSaving(true);
    try {
      // ìƒìœ„ ì‚¼ê° ë¹„êµìŒ ëª¨ë‘ PATCH
      const patchPromises: Promise<unknown>[] = [];
      for (let i = 0; i < criteria.length; i++) {
        for (let j = i + 1; j < criteria.length; j++) {
          const comp = comparisons.find(c =>
            (c.criteriaAId === criteria[i].id && c.criteriaBId === criteria[j].id) ||
            (c.criteriaAId === criteria[j].id && c.criteriaBId === criteria[i].id)
          );
          if (!comp) continue;
          // comp ë°©í–¥ì— ë§ê²Œ ê°’ ê²°ì •
          const value = comp.criteriaAId === criteria[i].id
            ? matrix.values[i][j]
            : matrix.values[j][i];
          patchPromises.push(
            apiService.patch<unknown>(
              `/api/service/evaluations/comparisons/${comp.id}/`,
              { value }
            )
          );
        }
      }
      await Promise.allSettled(patchPromises);

      // CR ê³„ì‚° (calculate/individual)
      try {
        const crRes = await apiService.post<unknown>(
          '/api/service/analysis/calculate/individual/',
          { evaluation_id: evaluationId }
        );
        const crData = crRes?.data as { consistency_ratio?: number } | undefined;
        const cr: number = crData?.consistency_ratio ?? 0;
        setMatrix(prev => prev ? { ...prev, consistencyRatio: cr, completed: true } : prev);

        if (cr > 0.1) {
          setShowHelper(true);
          showActionMessage('error', `ì¼ê´€ì„± ë¹„ìœ¨ CR=${cr.toFixed(3)} > 0.1 â€” ë¹„êµê°’ì„ ì¬ê²€í† í•´ì£¼ì„¸ìš”.`);
          return;
        }
      } catch {
        // CR ê³„ì‚° ì‹¤íŒ¨ ì‹œ ì™„ë£Œ ì§„í–‰
      }

      showActionMessage('success', 'í‰ê°€ê°€ ì €ì¥ë˜ì—ˆìŠµë‹ˆë‹¤.');
      setTimeout(() => onComplete(), 1000);
    } catch (error) {
      showActionMessage('error', 'ì €ì¥ì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.');
    } finally {
      setIsSaving(false);
    }
  };

  // â”€â”€â”€ ë¡œë”© / ì—ëŸ¬ ìƒíƒœ â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  if (isLoading) {
    return (
      <div className="page-evaluator">
        <div className="page-content content-width-evaluator flex items-center justify-center" style={{ minHeight: '16rem' }}>
          <div className="text-center">
            <div className="text-4xl mb-4">â³</div>
            <p className="text-lg">í‰ê°€ ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘...</p>
          </div>
        </div>
      </div>
    );
  }

  if (loadError || !matrix) {
    return (
      <div className="page-evaluator">
        <div className="page-content content-width-evaluator">
          <div className="page-header">
            <Button variant="secondary" onClick={onBack}>â† ë’¤ë¡œ</Button>
          </div>
          <Card className="text-center p-8">
            <div className="text-5xl mb-4">âš ï¸</div>
            <h2 className="text-xl font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
              í‰ê°€ ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì˜¬ ìˆ˜ ì—†ìŠµë‹ˆë‹¤
            </h2>
            <p className="mb-4" style={{ color: 'var(--text-secondary)' }}>
              ì´ í”„ë¡œì íŠ¸ì— ë°°ì •ëœ í‰ê°€ê°€ ì—†ê±°ë‚˜ ê¸°ì¤€ì´ 2ê°œ ë¯¸ë§Œì…ë‹ˆë‹¤.
            </p>
            <Button variant="secondary" onClick={loadData}>ë‹¤ì‹œ ì‹œë„</Button>
          </Card>
        </div>
      </div>
    );
  }

  // â”€â”€â”€ ë©”ì¸ ë Œë” â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€
  return (
    <div className="page-evaluator">
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${
          actionMessage.type === 'success' ? 'bg-green-100 text-green-800' :
          actionMessage.type === 'info'    ? 'bg-blue-100 text-blue-800' :
                                            'bg-red-100 text-red-800'
        }`}>
          {actionMessage.text}
        </div>
      )}

      <div className="page-content content-width-evaluator">
        <div className="page-header">
          <h1 className="page-title">ë‹¨ê³„ 2 â€” í‰ê°€í•˜ê¸° / ìŒëŒ€ë¹„êµ</h1>
          <p className="page-subtitle">
            í”„ë¡œì íŠ¸: <span className="font-medium">{projectTitle}</span>
          </p>
          <div className="mt-4">
            <Button variant="secondary" onClick={onBack}>
              í”„ë¡œì íŠ¸ ì„ íƒìœ¼ë¡œ
            </Button>
          </div>
        </div>

        {/* ì§„í–‰ë¥  */}
        <div className="card-enhanced p-4 mb-6">
          <div className="flex justify-between items-center mb-2">
            <span className="text-sm font-medium" style={{ color: 'var(--text-primary)' }}>
              ì „ì²´ ì§„í–‰ë¥ 
            </span>
            <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>
              {isSaving ? 'ì €ì¥ ì¤‘...' : `${getProgressPercentage()}% ì™„ë£Œ`}
            </span>
          </div>
          <div className="w-full rounded-full h-3" style={{ backgroundColor: 'var(--bg-elevated)' }}>
            <div
              className="h-3 rounded-full transition-all duration-300"
              style={{
                width: `${getProgressPercentage()}%`,
                background: 'linear-gradient(135deg, var(--accent-primary), var(--accent-secondary))'
              }}
            />
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* í–‰ë ¬ ì˜ì—­ */}
          <div className="lg:col-span-2">
            <Card title={matrix.name}>
              <div className="space-y-4">
                <MatrixGrid
                  items={matrix.items}
                  values={matrix.values}
                  onUpdate={handleMatrixUpdate}
                />

                {/* CR í‘œì‹œ */}
                {matrix.items.length >= 3 && matrix.consistencyRatio !== undefined && (
                  <div className="mt-4">
                    <div
                      className="p-3 rounded-lg border"
                      style={{
                        backgroundColor: matrix.consistencyRatio <= 0.1
                          ? 'var(--status-success-light)'
                          : 'var(--status-warning-light)',
                        borderColor: matrix.consistencyRatio <= 0.1
                          ? 'var(--status-success-border)'
                          : 'var(--status-warning-border)'
                      }}
                    >
                      <div className="flex items-center justify-between">
                        <span className="text-sm font-medium" style={{ color: 'var(--text-primary)' }}>
                          ë¹„ì¼ê´€ì„±ë¹„ìœ¨ (CR)
                        </span>
                        <span
                          className="font-semibold"
                          style={{
                            color: matrix.consistencyRatio <= 0.1
                              ? 'var(--status-success-text)'
                              : 'var(--status-warning-text)'
                          }}
                        >
                          {matrix.consistencyRatio.toFixed(3)}
                        </span>
                      </div>
                      <div className="text-xs mt-1" style={{ color: 'var(--text-secondary)' }}>
                        {matrix.consistencyRatio <= 0.1
                          ? 'âœ“ ì¼ê´€ì„±ì´ ì–‘í˜¸í•©ë‹ˆë‹¤'
                          : 'âš  ì¼ê´€ì„±ì„ ê°œì„ í•´ì£¼ì„¸ìš” (ê¸°ì¤€: â‰¤ 0.1)'}
                      </div>
                    </div>
                  </div>
                )}

                {/* ì™„ë£Œ ë²„íŠ¼ */}
                <div className="flex justify-end mt-6">
                  <Button
                    onClick={handleComplete}
                    variant="primary"
                    size="lg"
                    disabled={isSaving}
                  >
                    {isSaving ? 'ì €ì¥ ì¤‘...' : 'í‰ê°€ ì™„ë£Œ'}
                  </Button>
                </div>
              </div>
            </Card>
          </div>

          {/* ì‚¬ì´ë“œ íŒ¨ë„ */}
          <div className="lg:col-span-1">
            <div className="sticky top-6 space-y-4">
              {/* íŒë‹¨ ë„ìš°ë¯¸ ë²„íŠ¼ */}
              <Card>
                <div className="text-center">
                  <button
                    onClick={() => setShowHelper(!showHelper)}
                    className="w-full p-3 rounded-lg transition-all duration-300"
                    style={{
                      backgroundColor: 'var(--interactive-primary-light)',
                      color: 'var(--interactive-secondary)',
                      border: '1px solid var(--interactive-primary)'
                    }}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.backgroundColor = 'var(--interactive-primary)';
                      e.currentTarget.style.color = 'white';
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.backgroundColor = 'var(--interactive-primary-light)';
                      e.currentTarget.style.color = 'var(--interactive-secondary)';
                    }}
                  >
                    <span className="text-2xl">â“</span>
                    <div className="text-sm font-medium mt-1">íŒë‹¨ ë„ìš°ë¯¸</div>
                  </button>
                </div>
              </Card>

              {showHelper && (
                <JudgmentHelper
                  currentMatrix={matrix}
                  onClose={() => setShowHelper(false)}
                />
              )}

              {/* ì²™ë„ ì•ˆë‚´ */}
              <Card title="ìŒëŒ€ë¹„êµ ì²™ë„">
                <div className="space-y-2 text-xs">
                  {([
                    [1, 'ë™ë“±í•˜ê²Œ ì¤‘ìš”'],
                    [3, 'ì•½ê°„ ë” ì¤‘ìš”'],
                    [5, 'ì¤‘ìš”'],
                    [7, 'ë§¤ìš° ì¤‘ìš”'],
                    [9, 'ì ˆëŒ€ì ìœ¼ë¡œ ì¤‘ìš”'],
                  ] as [number, string][]).map(([v, label]) => (
                    <div key={v} className="flex justify-between">
                      <span style={{ color: 'var(--text-primary)', fontWeight: 'var(--font-weight-semibold)' }}>
                        {v}
                      </span>
                      <span style={{ color: 'var(--text-secondary)' }}>{label}</span>
                    </div>
                  ))}
                  <div className="text-center mt-2" style={{ color: 'var(--text-muted)' }}>
                    2, 4, 6, 8ì€ ì¤‘ê°„ê°’
                  </div>
                </div>
              </Card>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PairwiseEvaluation;
